.card {
  background: #fff;
  border-radius: 10px;
  height: 325px;
  margin: 1rem;
  position: relative;
  width: 325px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 60%;
    overflow: hidden;
    /* border-radius: 10px; */
  text-align: center;
  /* background:#6CADDC; */
  background-color: rgb(51, 83, 132);
  background-image: linear-gradient(rgb(51, 83, 132), rgb(52, 58, 64));
}
.card > .img-container > img {
  width: 60%
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}

.card > .content > ul {
  list-style-type: none;
}

.card > .content > ul > li {
  margin-top: 18px;
}

.card > .remove {
  color: #a01631;
  font-size: 18px;
  position: absolute;
  top: 7px;
  right: 12px;
  cursor: pointer;
}

.card > .remove:hover {
  color: #d50000;
}

.wrapper {
  padding-top: 50px;
  background: #ECEFF1;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

.jumbotron {
  padding: 30px 0;
  height: 300px;
  color: black;
  background-color: #2cbbd8;
  display: flex;
  align-items: center;
  align-content: center;
  text-align: center;
  vertical-align: middle;
  /* flex-direction: column; */
  justify-content: center;
  width: 100%;
  max-width: 100%; /* allows the jumbotron to adjust on smaller widths */
  border-bottom: 5px solid #db162f;
  margin-bottom: 10px;
}


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

